// Main JS module
// objectFitImages polyfill
import objectFitImages from "object-fit-images";

$(function () {
    objectFitImages();
});

$(".slider").slick({
    infinite: true,
    slidesToShow: 1,
    slidesToScroll: 1, 
});

$(document).ready(() => {
    viewportCheck();
    checkElements();
});
$(window).resize(viewportCheck);
$(window).scroll(checkElements)

function viewportCheck() {
    const viewport = $("meta[name='viewport']");
    if (screen.width >= 480) {
        viewport.attr("content", "width=480");
    } else {
        viewport.attr("content", "width=device-width, initial-scale=1");
    }
}

const burgerMenu = $(".burger-menu");
const btnHeight = $(".top__btn").outerHeight();
const topSectionTop = $("#top-section").offset().top;

function checkElements() {
    const topBtnTop = $(".top__btn").offset().top + btnHeight;
    if ($(window).scrollTop() > topSectionTop) {
        burgerMenu.addClass("menu-fixed");
    } else {
        burgerMenu.removeClass("menu-fixed");
    }
    if ($(window).scrollTop() > topBtnTop && $(window).scrollTop() < $("#form-section").offset().top - $(window).outerHeight()) {
        $(".btn-wrapper-fixed").addClass("btn-wrapper-fixed-active");
    } else {
        $(".btn-wrapper-fixed").removeClass("btn-wrapper-fixed-active");
    }
}

burgerMenu.on("click", () => {
    $(".menu").addClass("menu-active");
    burgerMenu.addClass("hidden");
    $("body").css("overflow", "hidden");
})

$(".menu__close, .navbar a, .menu__btn").on("click", closeMenu);

function closeMenu() {
    $(".menu").removeClass("menu-active");
    burgerMenu.removeClass("hidden");
    $("body").css("overflow", "auto");
}

$("#video-poster").on("click", function () {
    const video = $("#video");
    video.attr("preload", "auto");
    $(this).addClass("poster-disabled");
    setTimeout(() =>{playVideo(video);}, 300);
})

function playVideo(video) {
    video.attr("autoplay", "auto");
    video.get(0).play();
}

$(".scroll").click(function (e) {
    e.preventDefault();
    var target = this.hash,
        $target = $(target);
    $("html, body").stop().animate({
        scrollTop: $target.offset().top
    }, 600);
});